
export const Card = ({ children }) => {
    return (
        <div>
            <div className="bg-white rounded-md p-3 mb-5">
                {children}
            </div>
           
        </div>
    )
}
export const InfoCard = ({ param="bg-white", children }) => {
    return (
        <div className={" rounded-md p-5 " + param}>
            {children}
        </div>
    )
}

